import { React, Component }from 'react'
import './card.css'
import ScrollAnimation from 'react-animate-on-scroll';


class Card extends Component {
    render() {
        return ( 
<ScrollAnimation duration="1" animateIn="fadeIn isvisible" animateOnce="true" offset="75" >

<div className="image-box"
  style={{backgroundImage: `url(${this.props.background || "https://miro.medium.com/max/1600/1*_hVbpPdAN9kyZAIhezPbNg.gif"})`}}>
    <div className="inner">
        <h1 className="shadow somepadding">
            {this.props.heading}
        </h1>
        <h3 className="shadow somepadding">
            {this.props.subheading}
        </h3>
        <h3 className="shadow somepadding">
            {this.props.subsubheading}
        </h3>
    </div>
</div>
</ScrollAnimation>


)}
}

export default Card;